import { Currency } from '@uniswap/sdk-core'
import { CSSProperties, MutableRefObject, useCallback, useMemo, useState } from 'react'
import { FixedSizeList } from 'react-window'
import { useActiveWeb3React } from 'hooks/web3'
import useUSDCPrice from 'hooks/useUSDCPrice'
import { useUSDCValue } from 'hooks/useUSDCPrice'
import { useCurrencyBalance } from 'state/wallet/hooks'
import { WrappedTokenInfo } from 'state/lists/wrappedTokenInfo'
import NormalTokenItem from './NormalTokenItem'

function currencyKey(currency: Currency): string {
  return currency.isToken ? currency.address : 'ETHER'
}

function CurrencyRow({
  currency,
  onSelect,
  style,
  onChangeCount,
  onChangeWorth,
}: {
  currency: Currency
  onSelect: () => void
  style: CSSProperties
  onChangeCount: () => void
  onChangeWorth: (tokenWorth: number) => void
}) {
  const { account } = useActiveWeb3React()  
  const tokenPrice = useUSDCPrice(currency)
  const tokenBalance = useCurrencyBalance(account ?? undefined, currency)
  const value = useUSDCValue(tokenBalance)

  if (tokenPrice?.equalTo(0)) {
    return null
  }
  
  if (tokenBalance?.equalTo(0)) {
    return null
  }

  onChangeCount()
  onChangeWorth(Number(value?.toSignificant()))

  // only show add or remove buttons if not on selected list
  return (
    <NormalTokenItem currency={currency} style={style} onSelect={onSelect} />
  )
}

const BREAK_LINE = 'BREAK'
type BreakLine = typeof BREAK_LINE
function isBreakLine(x: unknown): x is BreakLine {
  return x === BREAK_LINE
}

export default function CurrencyList({
  height,
  currencies,
  otherListTokens,
  onCurrencySelect,
  fixedListRef,
  onChangeWorth
}: {
  height: number
  currencies: Currency[]
  otherListTokens?: WrappedTokenInfo[]
  onCurrencySelect: (currency: Currency) => void
  fixedListRef?: MutableRefObject<FixedSizeList | undefined>
  onChangeWorth: (tokenWorth: number) => void
}) {
  const [count, setCount] = useState(0)

  const addCount = () => {
    setCount(count + 1)
  }

  const itemData: (Currency | BreakLine)[] = useMemo(() => {
    if (otherListTokens && otherListTokens?.length > 0) {
      return [...currencies, BREAK_LINE, ...otherListTokens]
    }
    return currencies
  }, [currencies, otherListTokens])

  const Row = useCallback(
    function TokenRow({ data, index, style }) {
      const currency = data[index]
      const handleSelect = () => currency && onCurrencySelect(currency)

      if (currency) {
        return (
          <CurrencyRow
            style={style}
            currency={currency}
            onSelect={handleSelect}
            onChangeCount={addCount}
            onChangeWorth={onChangeWorth}
          />
        )
      } else {
        return null
      }
    },
    [ onCurrencySelect ]
  )

  const itemKey = useCallback((index: number, data: typeof itemData) => {
    const currency = data[index]
    if (isBreakLine(currency)) return BREAK_LINE
    return currencyKey(currency)
  }, [])

  console.log("pooh, count = ", count)

  return (
    <FixedSizeList
      height={67 * count}
      ref={fixedListRef as any}
      width="100%"
      itemData={itemData}
      itemCount={itemData.length}
      itemSize={67}
      itemKey={itemKey}
      style={{overflow: 'hidden'}}
    >
      {Row}
    </FixedSizeList>
  )
}
