import React, { useCallback, useMemo, useState } from 'react'
import { Currency, Token } from '@uniswap/sdk-core'
import { useActiveWeb3React } from 'hooks/web3'
import { useAllTokens } from 'hooks/Tokens'
import { ExtendedEther } from 'constants/tokens'
import { useTokenComparator } from 'components/SearchModal/sorting'
import Hero from './components/Hero'
import Investments from './components/Investments'
import WalletAndAllocation from './components/WalletAndAllocation'

const Dashboard: React.FC = () => {
  const { chainId } = useActiveWeb3React()
  const allTokens = useAllTokens()
  const [invertSearchOrder] = useState<boolean>(false)
  const [worth, setWorth] = useState(0)

  const tokenComparator = useTokenComparator(invertSearchOrder)

  const sortedTokens: Token[] = useMemo(() => {
    return Object.values(allTokens).sort(tokenComparator)
  }, [allTokens, tokenComparator])

  const ether = useMemo(() => chainId && ExtendedEther.onChain(chainId), [chainId])

  const sortedTokensWithETH: Currency[] = useMemo(() => {
    return ether ? [ether, ...sortedTokens] : sortedTokens
  }, [ether, sortedTokens])

  const handleAddWorth = (tokenWorth: number) => {
    setWorth(worth + tokenWorth)
  }

  const handleCurrencySelect = useCallback(
    (currency: Currency) => {
      
    }, []
  )
  
  return (
    <>
      <Hero worth={worth} />
      <Investments />
      <WalletAndAllocation currencyList={sortedTokensWithETH} changeWorth={handleAddWorth} onCurrencySelect={handleCurrencySelect} />
    </>
  )
}

export default Dashboard
