import React, { CSSProperties } from 'react'
import { Currency } from '@uniswap/sdk-core'
import { useActiveWeb3React } from 'hooks/web3'
import useUSDCPrice from 'hooks/useUSDCPrice'
import { useUSDCValue } from 'hooks/useUSDCPrice'
import { useCurrencyBalance } from 'state/wallet/hooks'
import CurrencyLogo from 'components/CurrencyLogo'

interface NormalTokenItemProps {
  currency: Currency
  style: CSSProperties
  onSelect: () => void
}

const NormalTokenItem: React.FC<NormalTokenItemProps> = ({ currency, style, onSelect }) => {
  const { account } = useActiveWeb3React()  
  const tokenPrice = useUSDCPrice(currency)
  const tokenBalance = useCurrencyBalance(account ?? undefined, currency)
  const value = useUSDCValue(tokenBalance)

  return (
    <div className="flex items-center hover:bg-linen cursor-pointer py-12 px-24 rounded-10 text-16" style={style} onClick={() => {onSelect()}}>
      <CurrencyLogo currency={currency} size={'24px'} />
      <div className="flex-grow leading-1.25 ml-24">
        <div className="flex justify-between relative">
          <p className="font-700 uppercase mb-8">{currency.name}</p>
          <span className="text-right">
            <div className="flex items-center uppercase mb-8 font-700">
              <div>
                ${value?.toSignificant()}
              </div>
            </div>
          </span>
        </div>
        <div className="flex justify-between text-dark-64 text-14 tablet:text-16">
          <p>${tokenPrice?.toSignificant()}</p>          
          <span className="text-right">
            <span className="text-14 tablet:text-16">
              {tokenBalance?.toSignificant()}
              <span className="uppercase">{currency.name}</span>
            </span>
          </span>
        </div>
      </div>
    </div>
  )
}

export default NormalTokenItem
